'use strict';


import $ from 'jquery';
import scrollMonitor from 'scrollmonitor';

export default class SkillsSection {
  constructor() {

    let skillsElement = $('.skills-section');
    
    if (skillsElement.length) {
      let contentWatcher = scrollMonitor.create( skillsElement );
      
      contentWatcher.enterViewport(function(){
        let progressBars = $('.progress');

        progressBars.each(function(index, item) {
          let percentage = $(item).data('percentage');
          $(item).children('.progress-bar').animate({width: `${percentage}%`}, 2000);
        });

      });
    
    }

    



  }
}
