// Main javascript entry point
// Should handle bootstrapping/starting application

'use strict';




import $ from 'jquery';

import Smooth from 'smooth-scrolling'

import Header from '../_modules/organisms/header/header';
import HeroSection from '../_modules/organisms/hero-section/hero-section';
import AboutSection from '../_modules/organisms/about-section/about-section';
import SkillsSection from '../_modules/organisms/skills-section/skills-section';
import PortfolioSection from '../_modules/organisms/portfolio-section/portfolio-section';

$(() => {
  // const section = document.querySelector('.base');

  // const smooth = new Smooth({
  //   native: true,
  //   section: section,
  //   ease: 0.11,
  // });
  
  // smooth.init();


  $(".nav-item").on('click', function(event) {

    $('.site-nav').removeClass('open');
    $('.site-nav-content').removeClass('showing');

    if (this.hash !== "") {

      event.preventDefault();

      var hash = this.hash;
      hash.replace('/','');

      $('html, body').animate({
        scrollTop: $(hash).offset().top
      }, 800, function(){

        window.location.hash = hash;
      });
    }
  });


  new Header();
  new HeroSection(); // Activate Link modules logic
  new AboutSection();
  new SkillsSection();
  new PortfolioSection();
});
