'use strict';


import $ from 'jquery';
import scrollMonitor from 'scrollmonitor';

export default class PortfolioSection {
  constructor() {
    let portfolioItems = $('.portfolio-item');
    
    if (portfolioItems.length) {
      let portfolioItemWatcher = scrollMonitor.create( portfolioItems );

      portfolioItems.each(function(index, item) {
        let itemWatcher = scrollMonitor.create( item );
        
        itemWatcher.enterViewport(()=>{
          $(item).addClass('animate-in');
        });

      });
    
    }

  }
}
